.item__wrapper{
    padding: 30px 0;
}
.item__row{
    display: flex;
    .item__column{
        flex-basis: 100%;
        display: flex;
        align-items:center;
        flex-direction: column;
        &.column-info{
            padding: 15px 0;
            background-color: rgba(0,0,0,0.3);
            justify-content: center;
        }
    }
}
.item__img{
    img{
        height: 400px;
        width: 200px;
        object-fit: contain;
    }
}
.item__info{
    font-size: 20px;
    padding: 5px 0;
    width: 100%;
    margin:5px 0;
    text-align: center;
    .item__name{
        font-size: 22px;
        font-weight: 600;
        font-family: Roboto Slab;
    }
    select{
        width: 50%;
        padding: 5px;
        font-size: 16px;
        font-family: 'Ubuntu', sans-serif;
        border:1px solid $primaryColorFirst;
    }
    .add2cart{
        padding: 7px 10px;
        width: 200px;
        font-size: 20px;
        font-weight: 600;
        letter-spacing: 1.1px;
    }
}
.item__price{
    font-weight: 600;
    font-family: Ubuntu;
    span{
        font-weight: 400;
        font-family: 'Open Sans', sans-serif;
        &.price{
            color:$primaryColorFirst;
            font-weight:700;
            font-family: 'Ubuntu', sans-serif;
        }
    }
}
.item__row{
    .tabs{
        width: 100%;
        padding: 30px;
    }
    .tabs__content{
        position: relative;
    }
    .tabs__controls{
        display: flex;
        align-items:center;
        justify-content: center;
        width: 100%;
    }
    .tabs__link{
        padding: 8px 10px;
        background-color:$primaryColorFirst;
        flex-grow: 1;
        color:$textColorLight;
        text-align: center;
        font-size: 18px;
        cursor: pointer;
        font-family: 'Ubuntu', sans-serif;
        font-weight: 600;
        &.active{
            background-color:$primaryColorSecond;
        }
    }
    .tabs__link + .tabs__link{
        margin-left: 15px;
    }
    .tabs__item{
        padding: 15px;
        background-color:white;
        display: none;
        color:black;
        opacity: 0;
        transition: .3s all ease-in-out;
        &.active{
            display: block;
            opacity: 1;
        }
    }
}