.product-block{
    padding:15px;
    display: flex;
    flex-wrap: wrap;
    align-items:center;
    justify-content: center;
}
.product-block__item{
    padding: 15px 0;
    width: 23%;
    background:rgba(256,256,256,.1);
    height: 340px;
    margin: 15px 10px;
    overflow: hidden;
    border: 2px solid rgba($color: $primaryColorFirst, $alpha: .6);
    position: relative;
    transition: 0.3s all ease-in-out;
    &:hover{
        overflow: visible;
        border-bottom-color: transparent;
        .product-block__info{
            opacity: 1;
            transition: 0.3s all ease-in-out;
            z-index: 99;
        }
    }
}
.product-block__title{
    font-family: 'Ubuntu', sans-serif;
    font-size: 20px;
    text-align: center;
    padding: 15px 0;
    color:white;
}
.proudct-block__img{
    img{
        width: 100%;
        height: 250px;
        object-fit: contain;
    }
}
.product-block__price{
    font-size: 18px;
    text-align: center;
    font-weight: 600;
    color:$primaryColorFirst;
    margin-bottom: 5px;
    span{
        font-weight: 400;
        color:white;
    }
}
.product-block__info{
    opacity: 0;
    transition: 0.3s all ease-in-out;
    margin-top: 5px;
    background:rgba(0,0,0,0.6);
    border: 2px solid rgba($color: $primaryColorFirst, $alpha:1.0);
    margin-left: -2px;
    margin-right: -2px;
    margin-top:-2px;
    border-top:0;
    z-index: -1;
    position: relative;
    color:white;
    padding-bottom: 5px;
    .row{
        display: flex;
        text-align: center;
        padding: 5px;
        padding-top: 10px;
        .column{
            flex-basis: 100%;
        }
    }
}
.quantity-control{
    display: flex;
    align-items:center;
    justify-content: center;
    padding:10px 0;
}
.minus,.plus{
    background:$primaryColorFirst;
    width: 20px;
    font-size: 18px;
    color:$textColorLight;
    text-align: center;
}
.quantity{
    width: 20px;
    text-align: center;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
}
.add2cart{
    background:$primaryColorFirst;
    width: 150px;
    display: flex;
    align-items:center;
    justify-content: center;
    margin: 0 auto;
    padding: 5px;
    text-transform: uppercase;
    color:$textColorLight;
    font-size: 18px;
    font-family: 'Roboto Slab', sans-serif;
}