.header {
  height: 70px;
  background-color:rgba($color: $primaryColorFirst, $alpha: .4);
  display: flex;
  padding: 15px;
}

.header__block {
  display: flex;
  align-items: center;
  flex-basis: 100%;

  &:nth-child(2) {
    display: flex;
    justify-content: center;
  }

  &:nth-child(3) {
    display: flex;
    justify-content: flex-end;
  }
}

.phone-trigger{
  .fa{
    font-size: 25px;
  }
}
.phone-list__item {
  &+& {
    margin-top: 10px;
  }

  a {
    color: $textColorLight;
    font-size: 16px;
  }
}

.operating-block {
  p {
    font-size: 16px;
  }
}

.cart {
  a {
    color: $textColorLight;
    font-family: 'Ubuntu', sans-serif;
    font-size: 18px;
    font-weight: 600;
  }
}