.main-nav {
  height: 100%;
  position: fixed;
  width: 25%;
  display: flex;
  flex-direction: column;
  padding: 30px 0;
  z-index: 0;
  background-color:rgba(256,256,256,0.1);
  &.active{
    z-index: 99;
    width: 100%;
    background:url(../img/wood.png) center center;
  }
  .category-list__item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    position: relative;
    background: $primaryColorFirst;
    transition: 0.3s all ease-in-out;
    &::after {
      position: absolute;
      font-family: FontAwesome;
      content: '\f054 ';
      right: 15px;
      top: 0;
      line-height: 50px;
      color: $textColorLight;
    }

    >a {
      font-family: 'Ubuntu', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 28px;
      text-align: center;
      color: $textColorLight;
    }
  }
  .category-list__item + .category-list__item{
    margin-top: 20px;
  }
  .category-list__item:hover{
    .subCategory-list{
      opacity: 1;
      z-index: 99;
    }
  }
  .category-list__item:hover .main-nav{
    background-color:red;
  }
  .subCategory-list{
    position: absolute;
    right: -150px;
    background:$primaryColorFirst;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    padding: 15px;
    z-index: -5;
    opacity: 0;
    li + li{
      margin-top: 5px;
    }
    a{
      font-family: 'Open Sans', sans-serif;
      font-weight:600;
      color:$textColorLight;
      font-size: 16px;
    }
  }
}

.main-nav__block {
  &+& {
    margin-top: 30px;
  }

  &.social-block {
    margin-top: auto;
  }
}


.logo {
  font-family: Roboto Slab;
  font-style: normal;
  font-weight: normal;
  font-size: 50px;
  line-height: 72px;
  text-align: center;
  color: $textColorLight;
}
