.footer{
    background-color:rgba($color: $primaryColorFirst, $alpha: .4);
}
.footer__wrapper{
    display: flex;
    justify-content: space-between;
    padding: 15px 30px;
}
.footer__block{
    flex-basis: 100%;
    &:nth-child(2){
        display: flex;
        flex-direction: column;
    }
    &:nth-child(3){
        display: flex;
        justify-content: flex-end;
    }
    .social-list{
        margin-top: 30px;
    }
    .phone-list__item{
        a{
            font-size: 18px;
            font-weight: 700;
        }
    }
}
.page-list{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items:center;
    li + li{
        margin-top: 10px;
    }
    li{
        a{
            color:$textColorLight;
            font-size: 20px;
            font-weight: 700;
        }
    }
}
.footer__logo{
    font-size: 30px;
    font-family: Roboto Slab;
    color:$textColorLight;
    margin-bottom: 30px;
}