.logo-mobile {
    display: none;
    font-size: 25px;
    font-family: Roboto Slab;
    color: $textColorLight;
}

.burger-menu {
    display: none;
}

.menu-trigger{
    display: none;
    .fa{
        font-size: 25px;
    }
}
body.overflow {
    position: relative;
    height: auto;
    overflow: hidden;
    &::after {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, .6);
        z-index: 98;
        display: block;
        left: 0;
        top: 0;
    }
}

.phone-trigger {
    display: none;
}

@media screen and(max-width:1024px) {
    .main-nav {
        width: 50%;
        z-index: -1;
        opacity: 0;
        transform: translate(-100%, 0);
        transition: .3s all ease-in-out;
        &.active {
            overflow: scroll;
            opacity: 1;
            z-index: 99;
            transform: translate(0, 0);
        }
    }
    .menu-trigger{
        display: block;
    }
    .content-wrapper {
        width: 100%;
        margin-left: 0;
    }

    .operating-block {
        display: none;
    }

    .logo-mobile {
        display: block;
    }

    .burger-menu {
        display: block;
    }
}

@media screen and (max-width:1000px) {
    .category-list__wrapper {
        .category-list__item {
            width: 44%;
        }
    }
    .product-block__item{
        width: 44%;
    }
}

@media screen and(max-width:768px) {
    .category-list__wrapper {
        &#indexPage {
            .category-list__item {
                width: 44%;
            }
        }

        .category-list__item {
            width: 44%;
        }
    }
    .item__row{
        flex-direction: column;
    }
    .item__column + .item__column{
        margin-top: 30px;
    }
}

@media screen and(max-width:600px) {
    .footer__wrapper {
        flex-direction: column;
        align-items: center;
    }

    .footer__block+.footer__block {
        margin-top: 30px;
    }

    .footer__block {
        .phone-list {
            text-align: center;
        }
    }

    .category-list__wrapper {
        &#indexPage {
            .category-list__item {
                width: 80%;
                height: 300px;
            }
        }

        .category-list__item {
            width: 80%;
            height: 300px;
        }
    }
    .product-block__item{
        width: 80%;
    }
}

@media screen and(max-width:475px) {
    .header {
        position: relative;

        .phone-list {
            opacity: 0;
            z-index: -1;
            position: absolute;
            bottom: -100%;
            background-color: $primaryColorFirst;
            padding: 10px;
            transition: .3s all ease-in-out;

            &.active {
                opacity: 1;
                z-index: 99;
            }
        }
        .header__block:nth-child(1){
            align-items:center;
            justify-content: center;
        }
        .phone-trigger {
            display: block;
        }
    }
    .item__row{
        .tabs__controls{
            flex-direction: column;
            .tabs__link{
                flex-basis: 100%;
                flex-grow: 1;
            }
            .tabs__link + .tabs__link{
                margin-left: 0;
                margin-top: 5px;
            }
        }
    }
}

@media screen and(max-width:375px) {
    .category-list__wrapper {
        &#indexPage {
            .category-list__item {
                width: 80%;
                height: 250px;
            }
        }

        .category-list__item {
            width: 80%;
            height: 250px;
        }
    }
    .cart{
        a{
            font-size: 16px;
        }
    }
}