body {
    counter-reset: item;
}

.cart-list {
    padding: 30px 15px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.cart-list__item {
    &+& {
        margin-left: 15px;
    }
    margin-bottom: 15px;
    border:1px solid $primaryColorSecond;
    padding:15px;
    display: flex;
    align-items: center;
    background:rgba(256,256,256,.1);

    &::before {
        content: counter(item);
        counter-increment: item;
        background-color: $primaryColorFirst;
        padding: 10px;
        color: $textColorLight;
        font-weight: 700;
    }
}

.cart-item__img {
    img {
        height: 250px;
        width: 150px;
        object-fit: contain;
    }
}

.cart-item {
    display: flex;
}

.cart-item__name {
    font-size: 18px;
    font-weight: 600;
    margin-top: 15px;
    text-align: center;
    color:white;
}

.cart-item__info {
    display: flex;
    flex-direction: column;
}

.cart-item__price {
    margin-top: 15px;
    color: $primaryColorFirst;
    font-size: 20px;
    text-align: center;
    font-weight: 600;

    span {
        font-weight: 400;
        color:white;
    }
}

.cart-item__list {
    padding: 15px 0;

    li+li {
        margin-top: 5px;
    }

    li {
        color: white;
        position: relative;
        display: flex;
        align-items: center;

        &::before {
            content: '+';
            margin-right: 5px;
            font-weight: 700;
        }
    }
}

.cart-summary {
    text-align: center;
    margin-top: auto;
    color: white;
    font-size: 18px;
    margin-bottom: 30px;
}

.cart-total {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 30px;

    .title {
        font-size: 25px;
        margin-bottom: 0;
        padding-bottom: 0;
    }

    .row {
        padding: 15px 0;
        margin:  15px 0;
        display: flex;
        width: 100%;
        background:rgba($primaryColorFirst,.3);
    }

    .column {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-basis: 100%;
    }
}

.cart-total__info {
    font-size: 20px;
    font-family: 'Ubuntu', sans-serif;
    color:white;
   
    span {
        font-weight: 600;
        font-family: 'Open Sans', sans-serif;
        color: $primaryColorFirst;
    }
}

.checkout-button {
    height: 40px;
    color: $textColorLight;
    background-color: $primaryColorFirst;
    width: 250px;
    font-size: 20px;
}

.checkout-modal {
    background-color: white;
    position: fixed;
    padding:15px 0;
    width: 500px;
    height: 300px;
    top: 50%;
    left: 50%;
    margin-top: -150px;
    margin-left: -250px;
    z-index: -1;
    opacity: 0;
    transition: .3s all ease-in-out;
    &.active{
        opacity: 1;
        z-index: 99;
    }
    .checkout-button{
        display: block;
        margin: 0 auto;
        margin-top: 15px;
    }
}
.input-wrapper{
    width: 100%;
    padding: 15px;
    input{
        padding-left: 15px;
        width: 100%;
        height: 40px;
        border:1px solid $primaryColorFirst;
    }
}