body {
    font-family: 'Open Sans', sans-serif;
    background:url(../img/wood.png) center center;
    color:white;
}

.content-wrapper {
    width: 75%;
    margin-left: 25%;
    position: relative;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    .footer{
        margin-top: auto;
    }
}

.content {
    background-attachment: fixed;
    height: 100%;
    padding-top: 15px;
}

.category-list__wrapper {
    .category-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    &#indexPage .category-list__item{
        width: 30%;
        height: 250px;
        background-image: url(../img/door.jpeg);
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 15px;
        font-family: 'Ubuntu', sans-serif;
        position: relative;
        transition: 0.3s all ease-in-out;
        &::after {
            content: '';
            position: absolute;
            background-color: rgba($color: #000000, $alpha: .5);
            left: 0;
            top: 0;
            z-index: 0;
            width: 100%;
            height: 100%;
            transition: 0.3s all ease-in-out;
        }
        .category-list__title {
            font-size: 22px;
            font-weight: 500;
            z-index: 3;
            border:none;
            color: $textColorLight;
            transition: 0.3s all ease-in-out;
        }
    }
    .category-list__item {
        width: 23%;
        position: relative;
        display: flex;
        justify-content: center;
        flex-direction: column;
        background:none;
        align-items: center;
        margin: 10px;
        font-family: 'Ubuntu', sans-serif;
        position: relative;
        transition: 0.3s all ease-in-out;
        .category-list__img{
            width:100%;
            height: 200px;
            object-fit: cover;
            transition: 0.3s all ease-in-out;
        }
        .category-list__title{
            background:rgba($primaryColorFirst,0.3);
            color:white;
            font-weight: 600;
            font-size: 20px;
            padding: 10px 0;
            width: 100%;
            text-align: center;
            border:2px solid white;
            border-top:0;
            transition: 0.3s all ease-in-out;
        }
    }
    .category-list__item:hover{
        .category-list__title{
            border-bottom-right-radius: 5px;
            border-bottom-left-radius: 5px;
        }
        .category-list__img{
            border-top-right-radius: 5px;
            border-top-left-radius: 5px;
        }
    }

    

    .subCategory-list {
        position: absolute;
        opacity: 0;
        transition: 0.3s all ease-in-out;
        background: rgba($color: #000000, $alpha: .4);
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        >li {
            a {
                color: $textColorLight;
                font-weight: 500;
                font-size: 16px;
                transition: 0.3s all ease-in-out;
            }

            a:hover {
                color: $primaryColorFirst;
            }
        }

        >li+li {
            margin-top: 10px;
        }
    }
}

#indexPage .category-list__item:hover {
    border-radius: 10px;

    &::after {
        border-radius: 10px;
    }

    .category-list__title {
        opacity: 0;
    }

    .subCategory-list {
        opacity: 1;
        z-index: 5;
        transition: 0.3s all ease-in-out;
    }
}

.title {
    font-size: 30px;
    text-align: center;
    font-family: 'Ubuntu', sans-serif;
    position: relative;
    padding: 15px 0;
}

.number {
    font-family: 'Ubuntu', sans-serif;
    font-weight: 600;
}

.social-list {
    display: flex;
    justify-content: space-around;

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-basis: 100%;

        .fa {
            color: $textColorLight;
            font-size: 25px;
        }
    }
}

.shop-subs {
    margin-top: 30px;
    padding: 30px;
    padding-top: 0;

    p {
        font-size: 16px;
    }

    p+p {
        margin-top: 15px;
        text-align: justify;
    }
}
.breadcrumb{
    padding: 15px;
}
.breadcrumbs-list{
    display: flex;
}
.breadcrumbs-list__item{
    position: relative;
    border-bottom: 2px solid $primaryColorFirst;
    padding-bottom: 2px;
    font-weight: 600;
    color:white;
    font-family: 'Ubuntu', sans-serif;
    a{
        color:white;
    }
    &::after{
        content: '\f105  ';
        font-family: FontAwesome;
        margin-left: 8px;
        position: absolute;
        top: 2px;
    }
    &:last-child{
        border-bottom:0;
        &::after{
            display: none;
        }
    }
}
.breadcrumbs-list__item + .breadcrumbs-list__item{
    margin-left: 20px;
}
.link-underline{
    cursor: pointer;
    border-bottom:2px solid $primaryColorFirst;
    transition: .3s all ease-in-out;
    &:hover{
        border-bottom-color:$primaryColorSecond;
    }
}
.custom-list__item{
    display: flex;
    align-items:center;
    & + &{
        margin-top: 8px;
    }
    &::before{
        content:'\f1db ';
        font-family: FontAwesome;
        color:$primaryColorFirst;
        margin-right: 8px;
        font-size: 10px;
    }
}